import {
	order,
	getRelation_id
} from '@/api/goods.js'
import {
	my_income
} from '@/api/user.js'
const tpl = {
	data0: {
		"datetime": "40分钟前",
		"article_type": 0,
		"title": "uni-app行业峰会频频亮相，开发者反响热烈!",
		"source": "DCloud",
		"comment_count": 639
	},

};

export default {
	components: {},
	data() {
		return {
			sysH: uni.getSystemInfoSync().windowHeight,
			scrollLeft: 0,
			data:new Object(),
			isClickChange: false,
			tabIndex: 0,
			newsitems: [],
			tabBars: [{
				name: '淘宝',
				id: 'tb'
			}],
			items1: [
				'我的订单',
				'团队订单',
				'团队奖金'
			],
			current1: 0,
			activeColor1: '#333',
			activeBack1: '#E9CEA1',
			activeBorder1: 'transparent',
			deadBack1: 'transparent',
			deadBorder1: '#fff',
			deadColor1: '#fff',
			items: [
				'全部',
				'已付款',
				'已结算',
				'已关闭'
			],
			current: 0,
			activeBack: '#E9CEA1',
			activeBorder: 'transparent',
			activeColor: '#333',
			deadBack: '#fff',
			deadBorder: 'transparent',
			deadColor: '#969696',
			orders: [],
			spends: [],
			CLSD: [],
			closed: [],
			timer: new Object(),
			relation_id: "",
		}
	},
	onLoad() {
		this.newsitems = this.randomfn()
		this.init();
		console.log()
	},
	onNavigationBarButtonTap(e) {
		console.log(e);
		if (e.index == 0) {
			uni.navigateTo({
				url: '/pages/user/board'
			})
		}
	},
	methods: {
		init() {
			// uni.showLoading({
			// 	title:"正在加载..."
			// }) 
			console.log(uni.getStorageSync("user"));
			if (uni.getStorageSync("user").pid == "") {
				console.log(uni.getStorageSync("tbsqFlag"));
				if (uni.getStorageSync("tbsqFlag")) {
					getRelation_id({
						phone: uni.getStorageSync("user").phone
					}).then(res => {
						console.log(res.data);
						if (res.code == 200) {
							this.relation_id = res.data
							this.incomeData(uni.getStorageSync("user").id);
						}
					})
				} else {
					uni.showToast({
						title: '你还没有授权淘宝',
						icon: 'none'
					})
				}
			} else {
				this.relation_id = uni.getStorageSync("user").pid;
				console.log(uni.getStorageSync("user").id);
				this.incomeData(uni.getStorageSync("user").id);
			}
		},
		incomeData(id) {
			my_income({
				id:id
			}).then(res => {
				console.log(id);
				console.log(JSON.stringify(res.data));
				if(res.code==200){
					this.data=res.result[0];
				}
			});
		},
		goDetail(e) {
			uni.navigateTo({
				url: '/pages/template/tabbar/detail/detail?title=' + e.title
			});
		},
		close(index1, index2) {
			uni.showModal({
				content: '是否删除本条信息？',
				success: (res) => {
					if (res.confirm) {
						this.newsitems[index1].data.splice(index2, 1);
					}
				}
			})
		},
		loadMore(e) {
			setTimeout(() => {
				this.addData(e);
				console.log(e);
			}, 1200);
		},
		addData(e) {
			if (this.newsitems[e].data.length > 30) {
				this.newsitems[e].loadingText = '没有更多了';
				return;
			}
			for (let i = 1; i <= 10; i++) {
				this.newsitems[e].data.push(tpl['data' + Math.floor(Math.random() * 5)]);
			}
		},
		async changeTab(e) {
			let index = e.target.current;
			if (this.newsitems[index].data.length === 0) {
				this.addData(index)
			}
			if (this.isClickChange) {
				this.tabIndex = index;
				this.isClickChange = false;
				return;
			}
			let tabBar = await this.getElSize("tab-bar"),
				tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;

			for (let i = 0; i < index; i++) {
				let result = await this.getElSize(this.tabBars[i].id);
				width += result.width;
				console.log(width);
			}
			let winWidth = uni.getSystemInfoSync().windowWidth,
				nowElement = await this.getElSize(this.tabBars[index].id),
				nowWidth = nowElement.width;
			if (width + nowWidth - tabBarScrollLeft > winWidth) {
				this.scrollLeft = width + nowWidth - winWidth;
			}
			if (width < tabBarScrollLeft) {
				this.scrollLeft = width;
			}
			this.isClickChange = false;
			this.tabIndex = index; //一旦访问data就会出问题
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
					console.log(data);
				}).exec();
			})
		},
		async tapTab(e) { //点击tab-bar
			let tabIndex = e.target.dataset.current;
			if (this.newsitems[tabIndex].data.length === 0) {
				this.addData(tabIndex)
			}
			if (this.tabIndex === tabIndex) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = tabIndex;
			}
		},
		randomfn() {
			let ary = [];
			for (let i = 0, length = this.tabBars.length; i < length; i++) {
				let aryItem = {
					loadingText: '没有更多了',
					data: []
				};
				if (i < 1) {
					for (let j = 1; j <= 10; j++) {
						aryItem.data.push(tpl['data' + Math.floor(Math.random() * 5)]);
					}
				}
				ary.push(aryItem);
			}
			return ary;
		}
	}
}
